/* General Reset and Setup */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

/* Background Image and Blur Effect */
.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* You can update this image */
    background-image: url('https://images.unsplash.com/photo-1542861250-b8833984594c?fit=crop&w=1400&h=900&q=80'); 
    background-size: cover;
    background-position: center;
    filter: blur(5px); 
    z-index: -1; 
}

/* Form Box (Central Container) */
.form-box {
    background: rgba(255, 255, 255, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 10px;
    padding: 30px;
    width: 350px; 
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(10px); 
    -webkit-backdrop-filter: blur(10px);
    color: #333; 
}

/* Input/Button Styling */
.form-box h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #111;
}

.input-group {
    margin-bottom: 15px;
}

.input-group label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
    font-size: 0.9em;
}

.input-group input {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.7);
    color: #333;
    font-size: 1em;
}

.btn {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 5px;
    background-color: #007bff; 
    color: white;
    font-size: 1em;
    cursor: pointer;
    margin-top: 10px;
    transition: background-color 0.3s;
}

.btn:hover {
    background-color: #0056b3;
}

/* Switch Link Styling */
.switch-link {
    text-align: center;
    margin-top: 15px;
    font-size: 0.9em;
}

.switch-link a {
    color: #007bff;
    text-decoration: none;
    font-weight: bold;
}

.switch-link a:hover {
    text-decoration: underline;
}